import React, { memo, useEffect } from "react";
import { StartButton } from "../../molecules/StartButton/StartButton";
import { ContentTitle } from "../../atoms/ContentTitle/ContentTitle";
import useOnceClick from "../../../hooks/useOnceClick";
import { Mb } from "../../atoms/Mb";

export interface StartButtonUnitProps {
  title: React.ReactElement<any, string | React.JSXElementConstructor<any>>;
  description: React.ReactElement<any, string | React.JSXElementConstructor<any>>;
  onceClick: () => void;
  initStartButtonClickable?: boolean;
}

/**
 * Functional component.
 * @param props 
 */
export const StartButtonUnit: React.FC<StartButtonUnitProps> = memo(({
  title,
  description,
  children,
  onceClick,
  initStartButtonClickable
}) => {

  const { clickHandle, initClickable } = useOnceClick(onceClick);

  useEffect(() => {
    const isClickable = initStartButtonClickable === void(0) ? false : initStartButtonClickable;
    if (isClickable) initClickable();
  }, [initStartButtonClickable]);

  return (
    <>
      <Mb
        size={ {
          pc: 39,
          tb: 37,
          sp: 27
        } }
      >
        <ContentTitle>{ title }</ContentTitle>
      </Mb>
      <Mb
        size={ {
          pc: 40,
          tb: 40,
          sp: 40
        } }
      >
        { children }
      </Mb>
      { description }
      <div onClick={ clickHandle }>
        <StartButton>スタート！</StartButton>
      </div>
    </>
  );
});
