import React, { memo } from "react";
import { StartButtonUnit } from "../../components/organisms/StartButtonUnit/StartButtonUnit";
import { RoundImage } from "../../components/atoms/RoundImage/RoundImage";
import useSetHeader from "../../hooks/useSetHeader";
import size from "../../constants/size";
import useSetQueryWithoutHistory from "../../hooks/useSetQueryWithoutHistory";
import Ruby from "../../components/atoms/Ruby/Ruby";
import { Mb } from "../../components/atoms/Mb";
import { AudioToggleButton } from "../../components/atoms/AudioToggleButton/AudioToggleButton";

export interface StartProps {
  setPageNum: React.Dispatch<React.SetStateAction<number>>;
}

/**
 * Functional component.
 * @param props 
 */
export const Start: React.FC<StartProps> = memo(({
  setPageNum
}) => {

  useSetHeader({
    color: "orange",
    prevHref: "/menus/menu8/main",
    prevTransitionKey: "slideAsVerticalPrev"
  });

  useSetQueryWithoutHistory();

  return (
    <>
      <StartButtonUnit
        onceClick={ () => {
          setPageNum(state => state + 1);
        } }
        title={ <><Ruby ruby="きゅうじょたい">救助隊</Ruby>に<Ruby ruby="でんわ">電話</Ruby>せよ！</> }
        description={
          <>
            <Mb
              size={ {
                pc: 40,
                tb: 40,
                sp: 40
              } }
            >
              <AudioToggleButton />
            </Mb>
          </>
        }
      >
        <RoundImage
          image="koudou_lifejacket_start.png"
          ratio={ size.illustRatio }
        />
      </StartButtonUnit>
    </>
  );
});