import React, { memo, useEffect } from "react";
import styled from "styled-components";
import { sourceHanSansJpBold } from "../../../styles/fonts";
import { SeContainer } from "../../../containers";
import size from "../../../constants/size";

export interface CountTimerProps {
  count: number;
}

/**
 * Styled component.
 */
const Main = styled.div`
  display: inline-block;
  margin-top: ${ size.controlUnitTopMargin }px;
`;

const w = 43;
const Clock = styled.div`
  width: ${ w }px;
  background-image: url(/icon_time.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 auto;
  &:before {
    content: "";
    display: block;
    padding-top: 100%;
  }
`;

const Time = styled.div`
  ${ sourceHanSansJpBold }
  font-size: 36px;
  color: #F53636;
  text-align: center;
  margin: 0 auto;
  line-height: 1;
  &:after {
    content: "秒";
    font-size: 16px;
    margin-left: 2px;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const CountTimer: React.FC<CountTimerProps> = memo(({
  count
}) => {

  const { time } = SeContainer.useContainer();

  useEffect(() => {
    const [play, { stop }] = time;
    if (count === 3) play();
    if (count === 0) stop();
  }, [count]);

  useEffect(() => {
    const [play, { stop }] = time;
    return () => stop();
  }, []);

  return (
    <>
      <Main>
        <Clock />
        <Time>{ count }</Time>
      </Main>
    </>
  );
});
