import { useState } from "react";

const useOnceClick = (fn: (prop?: any) => void) => {

  const [clicked, setIsClicked] = useState(false);

  const clickHandle = (prop?: any) => {
    setIsClicked(true);
    if (!clicked) fn(prop);
  }

  return {
    clickHandle: clickHandle,
    initClickable: () => setIsClicked(false)
  };
}

export default useOnceClick;
