import React, { memo, useState, useEffect } from "react";
import styled from "styled-components";
import { SeContainer } from "../../../containers";
import time from "../../../constants/time";
import useCheckIsTouchDevice from "../../../hooks/useCheckIsTouchDevice";
import { SelectButtonIcon, SelectButtonIconProps } from "../../atoms/SelectButtonIcon/SelectButtonIcon";
import { SelectButtonBar } from "../../atoms/SelectButtonBar/SelectButtonBar";

export interface SelectButtonProps {
  isCorrect: boolean;
  isActiveDisplaySymbol: SelectButtonIconProps["isActiveDisplaySymbol"];
  symbol: SelectButtonIconProps["symbol"];
  disablePointerEvents?: boolean;
  onClick: () => void;
}

/**
 * Styled component.
 */
interface MainProps {
  isActive: boolean;
  hover: boolean;
  isActiveDisplaySymbol: SelectButtonProps["isActiveDisplaySymbol"];
  disablePointerEvents: SelectButtonProps["disablePointerEvents"];
}
const Main = styled.div.attrs<MainProps>(
  (props) => ({
    style: {
      pointerEvents: props.isActiveDisplaySymbol || props.disablePointerEvents
        ? "none"
        : "auto"
    }
  })
)<MainProps>`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  cursor: ${ props => props.isActive ? "auto" : "pointer" };
  user-select: none;
`;

const SelectButtonBarWrapper = styled.div`
  position: absolute;
  width: 100%;
`;

/**
 * Functional component.
 * @param props 
 */
export const SelectButton: React.FC<SelectButtonProps> = memo(({
  children,
  isCorrect,
  isActiveDisplaySymbol,
  symbol,
  disablePointerEvents,
  onClick
}) => {

  // Play sound.
  const { correct, inCorrect } = SeContainer.useContainer();

  // Set hover.
  const [hover, setHover] = useState(false);
  const isTouchDevice = useCheckIsTouchDevice();

  // Set active button.
  const [clickAction, setClickAction] = useState(false);
  useEffect(() => {
    if (clickAction) {

      const [play] = isCorrect ? correct : inCorrect;
      play();

      if (!isCorrect) setTimeout(() => setClickAction(false), time.soundDistance);
    }
  }, [clickAction]);

  return (
    <>
      <Main
        isActive={ clickAction }
        hover={ hover }
        isActiveDisplaySymbol={ isActiveDisplaySymbol }
        role="button"
        disablePointerEvents={ disablePointerEvents }
        onClick={ () => {
          if (!clickAction) setClickAction(true);
          onClick();
        } }
        onMouseOver={ () => {
          if (!isTouchDevice) setHover(true);
        } }
        onMouseOut={ () => setHover(false) }
      >
        <SelectButtonBarWrapper>
          <SelectButtonBar isActive={ hover }>{ children }</SelectButtonBar>
        </SelectButtonBarWrapper>
        <SelectButtonIcon
          symbol={ symbol }
          isActiveDisplaySymbol={ isActiveDisplaySymbol }
          isActiveColor={ isCorrect || hover }
          isActionAnimation={ clickAction }
        />
      </Main>
    </>
  );
});
