import React, { memo } from "react";
import styled from "styled-components";
import { CountTimer, CountTimerProps } from "../../atoms/CountTimer/CountTimer";

export interface TopFixedTimerProps extends CountTimerProps {
}

/**
 * Styled component.
 */
 const Main = styled.div`
  margin: 0 auto;
  text-align: center;
  position: fixed;
  z-index: 2;
  top: 0;
  right: 0;
  left: 0;
`;

/**
 * Functional component.
 * @param props 
 */
export const TopFixedTimer: React.FC<TopFixedTimerProps> = memo(({
  count
}) => {
  return (
    <>
      <Main>
        <CountTimer count={ count } />
      </Main>
    </>
  );
});
