import React, { memo, useState, Dispatch, SetStateAction, useEffect } from "react";
import useSetHeader from "../../hooks/useSetHeader";
import { QuestionTitle } from "../../components/atoms/QuestionTitle/QuestionTitle";
import { Mb } from "../../components/atoms/Mb";
import useSetQueryWithoutHistory from "../../hooks/useSetQueryWithoutHistory";
import Ruby from "../../components/atoms/Ruby/Ruby";
import _ from "underscore";
import { TopFixedTimer } from "../../components/molecules/TopFixedTimer/TopFixedTimer";
import useInterval from "use-interval";
import { CallRescueOrder } from "../../components/atoms/CallRescueOrder/CallRescueOrder";
import { CallRescueDesc } from "../../components/atoms/CallRescueDesc/CallRescueDesc";
import dynamic from 'next/dynamic';
const Phone = dynamic(() => import("../../components/atoms/Phone/Phone"), { ssr: false});
import styled from "styled-components";
import time from "../../constants/time";

export interface MainProps {
  setPageNum: Dispatch<SetStateAction<number>>;
  pageNum: number;
}

/**
 * Styled component.
 */
const Wrapper = styled.div`
  max-width: 380px;
  margin: 0 auto;
`;

/**
 * Functional component.
 * @param props 
 */
export const Call: React.FC<MainProps> = memo(({
  setPageNum,
  pageNum
}) => {

  useSetHeader();
  useSetQueryWithoutHistory(pageNum);

  const [uniqueTimer, setUniqueTimer] = useState(time.callLimit);
  const [acceptCounting, setAcceptCounting] = useState(true);

  // Count
  useInterval(() => {
    if (acceptCounting) {
      setUniqueTimer(state => state - 1);
    }
  }, 1000);

  useEffect(() => {
    if (uniqueTimer <= 0) setPageNum(10);
  }, [uniqueTimer]);

  return (
    <>
      <TopFixedTimer count={ uniqueTimer } />
      <Mb
        size={ {
          pc: 39,
          tb: 37,
          sp: 27
        } }
      >
        <QuestionTitle icon="simulation_icon.png">
          <Ruby ruby="きゅうじょたい">救助隊</Ruby>に<Ruby ruby="でんわ">電話</Ruby>せよ！
        </QuestionTitle>
      </Mb>
      <Mb
        size={ {
          pc: 20,
          tb: 20,
          sp: 20
        } }
      >
        <Wrapper>
          <Mb
            size={ {
              pc: 36,
              tb: 36,
              sp: 36
            } }
          >
            <CallRescueOrder />
          </Mb>
          <Phone
            onCall={ () => {
              setAcceptCounting(false);
              setTimeout(() => {
                setPageNum(state => state + 1);
              }, 2000);
            } }
          />
        </Wrapper>
      </Mb>
      <CallRescueDesc><Ruby ruby="ともだち">友達</Ruby>が海に落ちた！</CallRescueDesc>
    </>
  );
});