import React, { memo, useEffect, useState } from "react";
import styled from "styled-components";
import { SelectButton } from "../../molecules/SelectButton/SelectButton";
import _ from "underscore";
import time from "../../../constants/time";
import customMedia from "../../../styles/customMedia";

export interface CallRescueSelectButtonUnitProps {
  onCorrect: () => void;
  correctQuestion: React.ReactElement<any, string | React.JSXElementConstructor<any>>;
  incorrectQuestion: React.ReactElement<any, string | React.JSXElementConstructor<any>>;
}

/**
 * Styled component.
 */
const Main = styled.div`
  & > div {
    margin-bottom: 22px;
    ${ customMedia.lessThan("sp")`
      margin-bottom: 18px;
    ` };
    &:last-child {
      margin-bottom: 0;
    }
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const CallRescueSelectButtonUnit: React.FC<CallRescueSelectButtonUnitProps> = memo(({
  onCorrect,
  correctQuestion,
  incorrectQuestion
}) => {
  
  // Set flug button top.
  const [isClickedTop, setIsClickedTop] = useState(false);

  // Set flug button bottom.
  const [isClickedBottom, setIsClickedBottom] = useState(false);
  const [isDisableBottom, setIsDisableBottom] = useState(false);

  useEffect(() => {
    let timer;
    if (isClickedTop) {

      // Dissable bottom button.
      setIsDisableBottom(true);

      // On correct function.
      timer = setTimeout(onCorrect, time.soundDistance);
    }
    return () => clearTimeout(timer);
  }, [isClickedTop]);

  const initialButtons = [
    <SelectButton
      key={ 1 }
      isCorrect={ isClickedTop }
      isActiveDisplaySymbol={ isClickedTop }
      disablePointerEvents={ isClickedTop }
      symbol="〇"
      onClick={ () => setIsClickedTop(true) }
    >
      { correctQuestion }
    </SelectButton>,
    <SelectButton
      key={ 2 }
      isCorrect={ false }
      isActiveDisplaySymbol={ isClickedBottom }
      disablePointerEvents={ isDisableBottom }
      symbol="×"
      onClick={ () => {
        setIsClickedBottom(true);
        setIsDisableBottom(true);
      } }
    >
      { incorrectQuestion }
    </SelectButton>
  ];

  // Set order as random.
  const [order, setOrder] = useState([]);
  useEffect(() => {
    setOrder(_.shuffle([...Array(initialButtons.length)].map((_, num) => num)));
  }, []);

  return (
    <>
      <Main>
        { initialButtons.sort((a, b) => {
          if( order[initialButtons.indexOf(a)] < order[initialButtons.indexOf(b)] ) return -1;
          if( order[initialButtons.indexOf(a)] > order[initialButtons.indexOf(b)] ) return 1;
          return 0;
        }) }
      </Main>
    </>
  );
});