import React, { memo } from "react";
import styled from "styled-components";
import { sourceHanSansJpBold } from "../../../styles/fonts";

export interface CallRescueDescProps {
}

/**
 * Styled component.
 */
const Main = styled.div`
  color: #1A1D4A;
  ${ sourceHanSansJpBold }
  text-align: center;
  font-size: 18px;
`;

/**
 * Functional component.
 * @param props 
 */
export const CallRescueDesc: React.FC<CallRescueDescProps> = memo(({
  children
}) => {
  return (
    <>
      <Main>{ children }</Main>
    </>
  );
});