import React, { memo } from "react";
import styled from "styled-components";
import { sourceHanSansJpBold } from "../../../styles/fonts";

export interface QuestionBalloonProps {
}

/**
 * Styled component.
 */
const color = "#F6C20E";
const Main = styled.div`
  ${ sourceHanSansJpBold }
  color: #1A1D4A;
  background-color: ${ color };
  border-radius: 10px;
  font-size: 18px;
  padding: 15px 9px;
  position: relative;
  text-align: center;
  &:before {
    content: "";
    position: absolute;
    bottom: -22px;
    left: 0;
    right: 0;
    width: 0;
    height: 0;
    margin: auto;
    border-style: solid;
    border-width: 22px 12.5px 0 12.5px;
    border-color: ${ color } transparent transparent transparent;    
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const QuestionBalloon: React.FC<QuestionBalloonProps> = memo(({
  children
}) => {
  return (
    <>
      <Main>{ children }</Main>
    </>
  );
});