import React, { memo } from "react";
import styled from "styled-components";
import size from "../../../constants/size";
import { sourceHanSansJpHeavy } from "../../../styles/fonts";
import time from "../../../constants/time";
import colors from "../../../constants/colors";
import { OpacityEffect } from "../OpacityEffect";

export interface SelectButtonIconProps {
  symbol: string;
  isActiveDisplaySymbol: boolean;
  isActiveColor: boolean;
  isActionAnimation: boolean;
}

/**
 * Styled component.
 */
interface MainProps {
  isActiveColor: SelectButtonIconProps["isActiveColor"];
  isActionAnimation: SelectButtonIconProps["isActionAnimation"];
}
const Main = styled.div.attrs<MainProps>(
  (props) => ({
    style: {
      backgroundColor: props.isActiveColor
        ? colors.selectButtonActiveColor.icon
        : colors.selectButtonNormalColor.icon,
      animation: props.isActionAnimation ? `bounsSelectButton ${ time.objectEffect }ms ease` : "none"
    }
  })
)<MainProps>`
  display: block;
  transition: background-color .3s ease;
  color: #fff;
  width: ${ size.selectButtonIconSize }px;
  height: ${ size.selectButtonIconSize }px;
  position: relative;
  line-height: ${ size.selectButtonIconSize }px;
  border-radius: 50%;
  @keyframes bounsSelectButton {
    from { transform: scale(.8); }
    to { transform: scale(1); }
  }
`;

const Symbol = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  ${ sourceHanSansJpHeavy }
  font-size: ${ size.selectButtonIconSize - 20 }px;
  text-align: center;
  line-height: 1.5;
`;

/**
 * Functional component.
 * @param props 
 */
export const SelectButtonIcon: React.FC<SelectButtonIconProps> = memo(({
  symbol,
  isActiveDisplaySymbol,
  isActiveColor,
  isActionAnimation
}) => {
  return (
    <>
      <Main isActiveColor={ isActiveColor } isActionAnimation={ isActionAnimation }>
        <Symbol>
          <OpacityEffect visible={ isActiveDisplaySymbol }>{ symbol }</OpacityEffect>
          <OpacityEffect visible={ !isActiveDisplaySymbol }>?</OpacityEffect>
        </Symbol>
      </Main>
    </>
  );
});