import React, { memo } from "react";
import styled from "styled-components";
import size from "../../../constants/size";
import { sourceHanSansJpBold } from "../../../styles/fonts";
import colors from "../../../constants/colors";
import customMedia from "../../../styles/customMedia";

export interface SelectButtonBarProps {
  isActive: boolean;
}

/**
 * Styled component.
 */
const Main = styled.div.attrs<SelectButtonBarProps>(
  (props) => ({
    style: {
      backgroundColor: props.isActive
        ? colors.selectButtonActiveColor.bar
        : colors.selectButtonNormalColor.bar
    }
  })
)<SelectButtonBarProps>`
  z-index: 1;
  margin-left: 10px;
  ${ sourceHanSansJpBold }
  padding: 12px 10px 12px ${ size.selectButtonIconSize / 2 + 10 }px;
  margin-left: ${ size.selectButtonIconSize / 2 }px;
  color: #1A1D4A;
  content: "";
  top: 0px;
  right: 0px;
  z-index: 0;
  box-shadow: rgb(73 143 139 / 30%) 0px 6px 6px;
  border-radius: 10px;
  transition: background-color .3s ease;
  font-size: 18px;
  ${ customMedia.lessThan("sp")`
    font-size: 14px;
  ` };
`;

/**
 * Functional component.
 * @param props 
 */
export const SelectButtonBar: React.FC<SelectButtonBarProps> = memo(({
  isActive,
  children
}) => {
  return (
    <>
      <Main isActive={ isActive }>{ children }</Main>
    </>
  );
});